<template>
  <el-tree
    ref="tree"
    :data="shelvesTreeData"
    node-key="id"
    :props="defaultProps"
    :default-expanded-keys="expandNodes"
    :expand-on-click-node="false"
    @node-click="handleNodeClick"
    :render-content="customRender"
  >
  </el-tree>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { baseTheme } from '@/utils/theme'
  import { generateTree } from '@/utils/generateTree'

  export default {
    name: 'Tree',
    data () {
      return {
        defaultProps: {
          label: 'name'
        }
      }
    },
    props: {
      clickedNodeData: {
        type: Object
      },
      shelvesTreeData: {
        type: Array
      },
      searchData: {
        type: Object
      },
      expandNodes: {
        type: Array
      }
    },
    watch: {

    },
    computed: {
      ...mapGetters(['systemTheme']),
      selectedNode () {
        return this.clickedNodeData
      },
      systemColor () {
        return baseTheme[this.systemTheme]
      }
    },
    methods: {
      // 节点点击事件
      handleNodeClick (data, node, nodeCom) {
        this.$emit('nodeClick', {data, node, nodeCom})
      },
      // 树节点自定义渲染格式 (JSX)
      customRender (createElement, {node, data, store}) {
        return (
          <span class="custom-tree-node" >
      <span>{node.label}</span>
        {
          (this.$refs.tree.getCurrentNode() && data.id === this.$refs.tree.getCurrentNode().id) &&
          <span class="active" style={{backgroundColor: this.systemColor}}></span>
        }
      </span>
      )
      },
      // 取消选中节点
      blurNode () {
        this.$refs.tree.setCurrentNode({})
      }
    }
  }
</script>

<style lang="scss" scoped>
  .custom-tree-node {
    position:relative;
    width:100%;

  .active {
    position: absolute;
    display: inline-block;
    width:10px;
    height:10px;
    right:10px;
    top:3px;
    border-radius:10px;
  }
  }
</style>



