<template>
  <div class="goods">
    <search
      @updateTree="updateTree"
      @search="search"
    />
    <div class="main">
      <div class="left-tree">
        <VuePerfectScrollbar class="scroll-area" :style="{height: flexHeight }">
          <tree
            ref="tree"
            :expandNodes="expandNodes"
            :clickedNodeData="clickedNodeData"
            :shelvesTreeData="shelvesTreeData"
            :searchData="searchParams"
            @nodeClick="handleNodeClick" />
        </VuePerfectScrollbar>
      </div>
      <div class="goods-content">
        <goods-detail
          ref="goodsDetail"
          :selectedId="selectedId"
          :searchData="searchParams"
        />
      </div>
    </div>
  </div>
</template>

<script>
  import VuePerfectScrollbar from 'vue-perfect-scrollbar'

  import Search from './Components/Search'
  import Tree from './Components/Tree'
  import GoodsDetail from './Components/GoodsDetail'

  import { generateTree } from '@/utils/generateTree'
  import { getShelvesTree } from '@/api/warehouse'
  import { getWarehouseList, deleteShelves, deleteShelvesList } from '@/api/warehouse'
  import { getAllMerchantsList } from '@/api/common'
  import { getShelvesWithCondition } from '@/api/warehouse'
  import { setSession, getSession } from '@/utils/session'
  import {mixins} from '@/mixins'

  export default {
    name: 'GoodsManage',
    mixins: [mixins],
    data () {
      const mh = document.documentElement.clientHeight - 240 > 200
        ? document.documentElement.clientHeight - 240
        : 200
      return {
        flexHeight: mh + 'px',
        searchParams: {},
        shelvesTreeData: [],
        expandNodes: [],
        clickedNodeData: {},
        selectedId: ''
      }
    },
    methods: {
      handleNodeClick ({data, node, nodeCom}) {
        this.clickedNodeData = data
        this.selectedId = data.id
        if(data.pId){
          this.searchParams = Object.assign(this.searchParams,{storageUuid:this.selectedId})
        }else{
          delete this.searchParams.storageUuid
        }
        this.$refs.goodsDetail.queryList(this.searchParams)
      },
      search (params) {
        this.searchParams = Object.assign(params,{storageUuid:this.selectedId})
        this.$refs.goodsDetail.queryList(this.searchParams)
      },
      updateTree (params) {
        this.selectedId = ''
        this.searchParams = params
        this.expandNodes = []
        this.clickedNodeData = {}
        this.searchParams = Object.assign({warehouseUuid:params})
        this.$refs.goodsDetail.queryList(this.searchParams)
        getShelvesTree({storageWarehouseUuid:params}).then(res => {
          this.shelvesTreeData = generateTree(res)
          res.forEach(node => {
            if (node.pId === '') this.expandNodes.push(node.id)
          })
        })
      },
    },
    components: {
      VuePerfectScrollbar,
      Search,
      Tree,
      GoodsDetail
    }
  }
</script>

<style lang="scss" scoped>
  .goods {
    width:100%;
    height:100%;
    display: flex;
    flex-direction: column;

    .main {
      height:100%;
      .warehouseList{
        margin: 10px 10px;
      }
      .left-tree {
        width:230px;
        /*padding:15px 0;*/
        height: 100%;
        box-sizing: border-box;
        float: left;
        background: #fff;
        border-right:2px solid darken($base-light-color, 5%);
        /*margin-bottom: 20px;*/
        .scroll-area {
          width:100%;
          height:100%;
        }
      }
      .goods-content {
        padding-left: 230px;
      }
    }

  }
</style>



