<template>
  <common-wrapper class="searchForm">
    <el-form :model="searchForm" ref="searchForm" label-width="50px">
      <el-row>
        <el-col :span="5">
          <el-form-item label="仓库" prop="warehouseUuid">
            <el-select placeholder="选择仓库" v-model="searchForm.warehouseUuid" class="w100p"
                       @change="warehouseChange">
              <el-option
                v-for="(item, index) in warehouseList"
                :key="index"
                :label="item.warehouseName"
                :value="item.warehouseUuid"/>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="商户" prop="merchantUuid">
            <el-select v-model="searchForm.merchantUuid" clearable filterable placeholder="选择商户" class="w100p"
                       @change="merchantsChange">
              <el-option
                v-for="(item, index) in merchantsList"
                :key="index"
                :label="item.userName"
                :value="item.userUuid"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="品牌" prop="productBrand">
            <el-select clearable placeholder="选择品牌" filterable  v-model="searchForm.productBrand" @change="brandChange" class="w100p">
              <el-option
                v-for="(item, index) in brandList"
                :key="index"
                :label="item"
                :value="item"
              >{{item}}
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="货品名称" prop="productName" label-width="80px">
            <el-input placeholder="货品名称" clearable v-model="searchForm.productName"></el-input>
          </el-form-item>
        </el-col>
        <!--<el-col :span="4">-->
          <!--<el-form-item label="库位" prop="shelveName">-->
            <!--<el-input placeholder="输入库位" clearable v-model="searchForm.shelveName"></el-input>-->
          <!--</el-form-item>-->
        <!--</el-col>-->
      </el-row>
      <el-row>
        <el-col :span="5">
          <el-form-item label="型号" prop="productModelName">
            <el-select clearable placeholder="选择型号" filterable  v-model="searchForm.productModelName" class="w100p">
              <el-option
                v-for="(item, index) in modelList"
                :key="index"
                :label="item"
                :value="item"
              >{{item}}
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <!--<el-col :span="5">-->
          <!--<el-form-item label="备注" prop="productRemark">-->
            <!--<el-input placeholder="货品备注" clearable v-model="searchForm.productRemark"></el-input>-->
          <!--</el-form-item>-->
        <!--</el-col>-->
        <el-col :span="5">
          <el-form-item label="类型" prop="productType">
            <el-select v-model="searchForm.productType" filterable clearable placeholder="请选择货品类型" class="w100p">
              <el-option label="小件" value="0"/>
              <el-option label="套件" value="1"/>
              <el-option label="单品" value="2"/>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="货品状态" prop="productStatus" label-width="80px">
            <el-select filterable clearable v-model="searchForm.productStatus" class="w100p" placeholder="选择状态">
              <el-option
                v-for="(item, index) in stateList"
                :key="index"
                :label="item.name"
                :value="item.id"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="4" class="flex-right">
          <el-button type="primary" @click.native="search">查询</el-button>
          <el-button @click="resetForm('searchForm')">重置</el-button>
        </el-col>
      </el-row>
    </el-form>
  </common-wrapper>
</template>

<script>
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import {setSession, getSession} from '@/utils/session'

import {getAllMerchantsList, getBrandModel } from '@/api/common'

export default {
  name: 'Search',
  data () {
    return {
      warehouseList: this.$store.getters.warehouseList,
      merchantsList: [],
      brandList: [],
      modelList: [],
      modelBrand: [],
      allModelList: [],
      searchForm: {
        warehouseUuid: '',
        merchantUuid: '',
        productBrand: '',
        productModelName: '',
        shelveName: '',
        remarks: '',
        goodsName: '',
        productType: '',
        productStatus: ''
      },
      stateList: [
        {
          id: 1,
          name: '待收货'
        },
        {
          id: 2,
          name: '已收货'
        },
        // {
        //   id: 3,
        //   name: '已质检'
        // },
        {
          id: 4,
          name: '库内存储'
        },
        {
          id: 5,
          name: '待质检'
        },
        {
          id: 6,
          name: '预出库'
        },
        {
          id: 7,
          name: '已备货'
        },
        {
          id: 8,
          name: '已发货'
        },
        {
          id: 9,
          name: '待发货'
        },
        {
          id: 10,
          name: '待维修'
        },
        {
          id: 11,
          name: '待下架'
        }
      ]
    }
  },
  props: {},
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.getMerchants()
    this.getBrandModel({warehouseUuid: getSession('warehouseUuid')})
    this.$emit('updateTree', this.searchForm.warehouseUuid)
    this.search()
  },
  methods: {
    // 获取商户
    getMerchants () {
      return new Promise(resolve => {
        let params = {warehouseUuid: this.searchForm.warehouseUuid}
        getAllMerchantsList(params).then(res => {
          if (!res.error) {
            this.merchantsList = res
            resolve()
          }
        })
      })
    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.allModelList = []
          this.modelList = []
          res.forEach((item, index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.allModelList.push(item2)
            })
          })
          this.modelList = [].concat(this.allModelList)
          this.modelBrand = res
        }
      })
    },
    // 仓库改变
    warehouseChange (value) {
      setSession('warehouseUuid', value)
      this.getMerchants().then(() => {
        this.searchForm.merchantUuid = ''
        this.searchForm.productBrand = ''
        this.searchForm.productModelName = ''
        // this.search()
        this.getBrandModel({warehouseUuid: this.searchForm.warehouseUuid})
        this.$emit('updateTree',this.searchForm.warehouseUuid)
      })
    },
    // 商户改变
    merchantsChange () {
      this.searchForm.productBrand = ''
      this.searchForm.productModelName = ''
      this.getBrandModel({warehouseUuid: this.searchForm.warehouseUuid, merchantUuid: this.searchForm.merchantUuid})
      // this.search()
    },
    // 品牌改变
    brandChange (value) {
      this.searchForm.productModelName = ''
      if (value !== '') {
        let model = []
        model = this.modelBrand.find(item => {
          return item.productBrand === value
        })
        this.modelList = model.productModel
      } else {
        this.modelList = [].concat(this.allModelList)
      }
      // this.search()
    },
    // 型号改变
    modelChange () {
      // this.search()
    },
    // 检索
    search () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  },
  components: {
    CommonWrapper,
    CommonTable
  }
}
</script>

<style lang="scss" scoped>
  .searchForm {
    margin-bottom: 0;
  }
  .el-form-item {
    margin-bottom: 10px;
  }
</style>



